div.view-Template {
    background: #cde;
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 0;
}

div.view-Template>div {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    margin: 0;
    padding: 3rem 0 0 0;
    overflow: auto;
    text-align: center;
}

div.view-Template>header {
    position: fixed;
    top: 0;
    bottom: auto;
    width: 100%;
    height: 3rem;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    z-index: 1;
    box-shadow: 0 3px 6px rgba(0, 0, 0, .5);
    border-radius: 0 0 1rem 1rem;
}

div.view-Template>header>* {
    flex: 0 0 auto;
}

div.view-Template>header>div {
    flex: 1 1 auto;
    text-align: center;
    margin: 0 1rem;
}

div.view-Template>header>div.pages {
    flex: 0 0 auto;
}

div.view-Template>div>article {
    display: inline-block;
    text-align: left;
    max-width: 800px;
    margin: 1rem .5rem;
    padding: .5rem;
    background: #eee;
    border-radius: .25rem;
    border: 1px solid #0003;
}

div.view-Template>div>article>nav {
    display: flex;
    width: 100%;
    justify-content: space-between;
    align-items: center;
    flex-direction: row;
    margin: 1rem 0 0 0;
}

div.view-Template>div>article>nav>* {
    flex: 0 0 auto;
}

div.view-Template>div>article>section {
    clear: both;
}

div.view-Template pre {
    white-space: pre;
}
